	module.exports = {"data":{"markdownRemark":{"html":"<p>最近特にキリ番アクセスカウントの踏み逃げ禁止なサイトが多いですが、訪問者側からすれば「踏み逃げのどこが悪いの？」と言った感じですね。 本来アクセスカウンタは現在までのアクセス数を数える目的のために存在するものです。おそらくキリのいいカウントを取ってもうれしいと思う人はごくわずかかと思われます。それに Web サイトと言う Web サイトを駆けずり回り資料を探す多忙人もいるわけです。 そんな人が検索サイトから偶然飛んできて、しかも偶然10,000カウントとか取ってしまっても、まず報告なんかしないと思います。また、どういうわけか踏み逃げ禁止と謳っているサイトに限り高確率で内容がショボいのも事実です。恐らくそのサイトが余りにつまらなくて管理人が苦し紛れに取った方法なんでしょうね。最悪のケースとしては、キリ番取って報告したら下手な絵(しかも非圧縮BMPファイル)を添付メールで送りつけられたとの話も聞いたこともあります。 私個人的にはキリ番報告自体はそれでコミュニケーションが成り立つのなら構わないと思いますが『強制』はよろしくないと思います。つまり、<em>『踏み逃げ禁止』『素通り禁止』は至極ネチケットに反する行為</em>であると考えています。</p>","frontmatter":{"cover":null,"date":"2002-08-08T05:58:23+09:00","strDate":"2002/8/7","title":"踏み逃げに関する考察"}}},"pathContext":{"path":"/ja/blog/2002/08/08-踏み逃げに関する考察/","langKey":"ja"}}

